@media only screen and (min-width: 500px) {
  .module1 {
    border: 1px white solid;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    background-color: red;
    align-items: center;
    width: 80vw;
    overflow: hidden;
  }
  .module1 .loadersSec {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .module1 .loadersSec #loaderpercent {
    color: white;
    font-size: 10rem;
    background: black;
    border-radius: 50px 50px 0 0;
  }
  .module1 .loadersSec progress {
    border: 2px white solid;
    border-radius: 1rem;
    overflow: hidden;
    background: white;
    height: 1rem;
  }
  .module1 .loadersSec .d3Env {
    font-size: 3rem;
    background: black;
    padding: 0 10 0 10;
    font-weight: 800;
    color: white;
    font-style: italic;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  }
}

@media only screen and (max-width: 500px) {
  .module1 {
    border: 2px orange solid;
    border-radius: 30px;
    height: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  .module1 .loadersSec {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .module1 .loadersSec #loaderpercent {
    color: orange;
    font-size: 4.5rem;
    font-weight: 600;
    background: white;
    border-radius: 10px 10px 0 0;
  }
  .module1 .loadersSec progress {
    border: 2px white solid;
    overflow: hidden;
  }
  .module1 .loadersSec .d3Env {
    font-size: 1.5rem;
    background: white;
    font-weight: 800;
    color: orange;
    font-style: italic;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  }
}
/*# sourceMappingURL=Module1.css.map */
@media only screen and (min-width: 500px) {
  .leftNav {
    position: absolute;
    width: 9vw;
    left: 0;
    height: 480px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  .leftNav > * {
    display: flex;
    align-items: center;
    justify-content: center;
    background: white;
    width: 100%;
    height: 100px;
    font-size: 3rem;
    border-radius: 0 10px 10px 0;
    cursor: pointer;
    transition-duration: .3s;
    border: 3px orange solid;
  }
  .leftNav .backwardpage:hover {
    transition-duration: .3s;
    color: orange;
    margin-left: 2rem;
    width: 140%;
    font-size: 4rem;
  }
  .leftNav .infoModels:hover {
    transition-duration: .3s;
    color: skyblue;
    margin-left: 2rem;
    width: 140%;
    font-size: 4rem;
  }
  .leftNav .forwardpage:hover {
    transition-duration: .3s;
    color: red;
    margin-left: 2rem;
    width: 140%;
    font-size: 4rem;
  }
}

@media only screen and (max-width: 500px) {
  .leftNav {
    display: none;
  }
}
/*# sourceMappingURL=LeftNav.css.map */
@media only screen and (min-width: 500px) {
  .rightNav {
    position: absolute;
    width: 9vw;
    right: 0;
    height: 480px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
  .rightNav > * {
    display: flex;
    align-items: center;
    justify-content: center;
