    font-size: 3rem;
    border-radius: 10px 0px 0px 10px;
    cursor: pointer;
    transition-duration: .3s;
    border: 3px skyblue solid;
  }
  .rightNav .musicToggle:hover {
    transition-duration: .3s;
    color: orange;
    margin-right: 2rem;
    width: 120%;
    font-size: 4rem;
  }
  .rightNav .options:hover {
    transition-duration: .3s;
    color: skyblue;
    margin-right: 2rem;
    width: 120%;
    font-size: 4rem;
  }
  .rightNav .copy-link:hover {
    transition-duration: .3s;
    color: red;
    margin-right: 2rem;
    width: 120%;
    font-size: 4rem;
  }
}

@media only screen and (max-width: 500px) {
  .rightNav {
    display: none;
  }
}
